<template>
    <!-- 集团利润信息 -->
    <div class="wrap-div" ref="wrapHeight">
        <!-- <div v-if="isday" ref="day_chart" class="bar-chart" :style = "{width:wrapWidth+'px',height:wrapHeight+'px'}"></div> -->
        <div v-if="ismon" ref="mon_chart" class="mon-chart" :style = "{width:wrapWidth+'px',height:wrapHeight+'px'}"></div>
        <div v-if="isyear" ref="year_chart" class="bar-chart" :style = "{width:wrapWidth+'px',height:wrapHeight+'px'}"></div>
    </div>
</template>
<script>
    const echarts = require('echarts/lib/echarts');
    require('echarts/lib/chart/line');
    require('echarts/lib/chart/pie');
    require('echarts/lib/component/tooltip');
    require('echarts/lib/component/legend');
    export default {
        data(){
            return{
                isyear:false,
                ismon:true,
                wrapHeight: 342,
                wrapWidth: 561,
            }
        },

        activated(){
            this.init();
        },
        mounted() {
            this.init();
        },
        watch:{
            '$store.state.JTweekTextIIV':'getweekText'
        },
        methods: {
            init(){
                this.getweekText();
                this.getDivHeight();
            },
            getDivHeight(){
                this.wrapHeight = this.$refs.wrapHeight.offsetHeight;
                this.wrapWidth = this.$refs.wrapHeight.offsetWidth;
            },

            //切换
            getweekText(){
                let JTtext = 2;
                    JTtext = this.$store.state.JTweekTextIIV;
                if(JTtext === 3){
                    this.ismon = false
                    setTimeout(()=>{
                        this.isyear = true;
                    })
                    this.getlist();
                }else if(JTtext === 2){
                    this.isyear = false;
                    setTimeout(()=>{
                        this.ismon = true;
                    })
                    this.getlist();
                }
                
            },

            getlist(){
                this.getAjax('/mktmfc/forms/getgroupreceivablemsg?datetype='+this.$store.state.JTweekTextIIV)
                .then((result)=>{
                    if(result.status === 200){

                    }
                })
                .then(()=>{
                    this.initEchart();
                    this.setEchart();
                   
                    
                })
            },

            initEchart(){
                if(this.isyear){
                    this.year_chart = echarts.init(this.$refs.year_chart);
                    this.year_chart.showLoading();
                }else if(this.ismon){
                    this.mon_chart = echarts.init(this.$refs.mon_chart);
                    this.mon_chart.showLoading();
                }
            },

            setEchart(){
                if(this.ismon){
                    this.mon_chart.hideLoading();
                    this.mon_chart.setOption({
                        tooltip: {
                            trigger: 'axis'
                        },
                        grid: {
                            left: '12%',
                        },
                        xAxis:  {
                            type: 'category',
                            boundaryGap: false,
                            data: ['1月','2月','3月','4月','5月','6月','7月','8月','9月','10月','11月','12月']
                        },
                        yAxis: {
                            name:"万(元)",
                            type: 'value',
                            axisLabel: {
                                // formatter: '{value} °C'
                            }
                        },
                        series: [
                            {
                                name:'恒利搅拌站(利润)',
                                type:'line',
                                itemStyle:{normal:{color: '#00c26f'}},
                                lineStyle: {
                                    normal: {
                                        width: 2,
                                        shadowColor: 'rgba(0,0,0,0.4)',
                                        shadowBlur: 10,
                                        shadowOffsetY: 10
                                    }
                                },
                                data:[321.22,20.85,320.95,0,0,0,0,0,0,0,0,0],
                                symbolSize: 5,
                                smooth: true,
                                areaStyle: {
                                    normal: {
                                        color: '#c5f0bb'
                                    }
                                },
                            },

                            {
                                name:'合辉搅拌站(利润)',
                                type:'line',
                                itemStyle:{normal:{color: '#91e5e7'}},
                                lineStyle: {
                                    normal: {
                                        width: 2,
                                        shadowColor: 'rgba(0,0,0,0.4)',
                                        shadowBlur: 10,
                                        shadowOffsetY: 10
                                    }
                                },
                                data:[156.6,0.11,4.40,0,0,0,0,0,0,0,0,0],
                                symbolSize: 5,
                                smooth: true,
                                areaStyle: {
                                    normal: {
                                        color: '#91e5e7'
                                    }
                                },
                            }
                        ]
                    })
                };

                //年
                if(this.isyear){
                    this.year_chart.hideLoading();
                    this.year_chart.setOption({
                        series: [
                            {
                                center: ['25%', '50%'],
                                type:'pie',
                                radius: ['60%', '66%'],
                                label: {
                                    normal: {
                                        position: 'center'
                                    }
                                },
                                
                                data:[
                                    {
                                        name:'本年',
                                        label: {
                                            normal: {
                                                formatter: '',
                                                textStyle: {
                                                    color:'#d3d3d3',
                                                    fontSize: 14
                                                }
                                            }
                                        },

                                    },
                                    {   
                                        value:633.03,
                                        name:'本年',
                                        label: {
                                            normal: {
                                                formatter: '\n{d}%',
                                                textStyle: {
                                                    fontSize: 28
                                                }
                                            }
                                        },
                                        itemStyle:{normal:{color: '#00c26f'}},
                                    },
                                    {
                                        value:633.03*4-633.03, 
                                        name:'计划',
                                        itemStyle:{normal:{color: '#cfe8dc'}},
                                        label: {
                                            normal: {
                                                formatter: `恒利搅拌站:${633.03}万(元)\n`,
                                                textStyle: {
                                                    color:'#d3d3d3',
                                                    fontSize: 14
                                                }
                                            }
                                        },
                                    }
                                ]
                            },
                            {
                                center: ['75%', '50%'],
                                type:'pie',
                                radius: ['60%', '66%'],
                                label: {
                                    normal: {
                                        position: 'center'
                                    }
                                },
                                data:[
                                    {
                                        name:'本年',
                                        label: {
                                            normal: {
                                                formatter: '',
                                                textStyle: {
                                                    color:'#d3d3d3',
                                                    fontSize: 14
                                                }
                                            }
                                        },

                                    },
                                    {   
                                        value:161.11,
                                        name:'本年',
                                        label: {
                                            normal: {
                                                formatter: '{d}%',
                                                textStyle: {
                                                    fontSize: 20
                                                }
                                            }
                                        },
                                        itemStyle:{normal:{color: '#008ae0'}},
                                    },
                                    {
                                        value:161.11*6-161.11, 
                                        name:'计划',
                                        itemStyle:{normal:{color: '#cfd8dc'}},
                                        label: {
                                            normal: {
                                                formatter: `合辉搅拌站:${161.11}万(元)`,
                                                textStyle: {
                                                    color:'#d3d3d3',
                                                    fontSize: 14
                                                }
                                            }
                                        },
                                    }
                                ]
                            },
                        ]
                    })
                }
            }
        }
    }
</script>
<style lang="less" scoped type="text/less">
    .wrap-div{
        height: 100%;
    }
</style>


